@use '../utils'as *;

.cards {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);

  gap: 50px;

  &__card {
    background: linear-gradient(58deg, #8c2626 1%, #564646 46%, #756aa5 100%);
    color: white;
    text-align: center;
    border: 2px solid black;
    padding: 30px;
    width: 200px;
    &__image-wrap {
      width: 200px;
    }
    &__image {
      max-width: 100%;
    }
    &__headline {
      margin-top: 0;
      @include header;
      word-wrap: break-word;
    }

    &__field {
      padding: 5px;
    }
  }
}
