@use '../utils/variables' as *;
@use '../utils/mixins' as *;

.working-zone {
  max-width: $maxWidthWork;
  margin: 0 auto;
  padding: 0 5px;
}

.invisible {
  overflow: hidden;
  height: 1px;
  width: 1px;
  border: 0;
  padding: 0;
  clip-path: insert(0px, 99.9%, 99.9%);
  font-size: 0px;
}

.inactive {
  pointer-events: none;
  opacity: 0.7;
}

.no-touch {
  pointer-events: none;
}

.hide {
  display: none;
}

.modal-window-background {
  filter: blur(10px);
  pointer-events: none;
}

.header-headline {
  @include header2;
  text-align: center;
}
.headline {
  @include header;
  text-align: center;
}

.animation {
  transition: all 0.5s;
  pointer-events: none;
}

.relocate-element {
  order: -2;
}

.disable {
  display: none;
}

.error-msg {
  @include header;
  font-weight: 600;
  margin: 20px 0;
  color: red;
}
