@use '../utils' as *;

.nav-buttons {
  display: flex;
  justify-content: center;
  align-items: baseline;
  gap: 30px;
}

.button {
  @include buttonText;
  background: $okButtonGradient;
  color: white;
  width: 200px;
  height: 50px;
  box-shadow: 3px 5px 7px 1px $shadowColor;
  border-radius: 10px;

  &:hover,
  &:focus {
    cursor: pointer;
    opacity: 0.9;
  }
}
