@use '../utils'as *;

.header {
  background-image: $headerGradient;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;

  &__wrap {
    @include flex(space-between);
    height: 40px;
    width: 100%;
  }

  &__headline {
    margin: 0;
    font-style: italic;
  }

  &__nav {
    padding-left: 20%;
    padding-right: 20%;
    width: 100%;

    @include res(1200px) {
      padding-left: 5%;
      padding-right: 5%;
    }

    &__list {
      @include flex(space-between);

      .link-active {
        color: white;
        pointer-events: none;
      }

      &__item {
        &__link {
          @include header;

          @include res(800px) {
            font-size: 1em;
          }

          @include res(400px) {
            font-size: 0.6rem;
          }

          &:hover {
            opacity: 0.5;
          }
        }
      }
    }
  }
}
