@use '../utils'as *;

.form {
  text-align: center;
  width: 40%;
  margin: 100px auto 0;
  &__input-wrap {
    position: relative;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__input-label {
    margin: 0 10px;
  }

  &__input {
    width: 600px;
    height: 30px;
  }

  &__input-radio {
    margin-left: 5px;
  }

  &__select {
    width: 50px;
    margin-left: 20px;
  }

  &__submit-wrap {
    margin-top: 20px;
  }

  &__checkbox {
    width: 20px;
    height: 20px;
  }
}
